@import "../../../styles/abstracts/variables";



.menu-bar {
  height: calc((100vh - #{$appHeader-height}));
  font-size: $font-size;
  width: 12rem;
  &__menu {
    margin-top: 2rem;
    padding: 3rem 0 0.8rem 2.5rem;
    width: 12rem;
    height: 80%;
    border-radius: 0 1rem 1rem 0;
    list-style: none;
    z-index: 300;
    overflow: hidden;
    transition: all .1s linear;
    overflow-y: auto;
    position: relative;
    &::-webkit-scrollbar {
      width: 6px; }
    &::-webkit-scrollbar-track {
      background-color: #ffffff;
      -webkit-box-shadow: inset 0 0 13px rgba(0, 0, 0, 0.52);
      border-radius: 10px;
      margin: 1rem; }
    &::-webkit-scrollbar-thumb {
      cursor: pointer;
      border-radius: 5px;
      background-color: #575757; }
    &-item {
      margin: 1.5rem 0;
      user-select: none;
      width: 8rem;
      display: flex;
      flex-direction: column;
      height: fit-content;
      .desc {
        width: fit-content;
        margin: 0 1.5rem;
        color: #000000;
        transition: all 0.5s;
        opacity: 0;
        visibility: hidden;
        &_active {
          visibility: visible;
          opacity: 1;
          transition-delay: 0.3s; } }
      .sub-item {
        max-height: 0;
        overflow: hidden;
        transition: max-height 0.5s ease-in-out;
        width: fit-content;
        &_link {
          min-width: 3.3rem;
          min-height: 3.3rem;
          display: flex;
          align-items: center;
          justify-content: center;
          text-decoration: none;
          margin: 1rem 0.6rem;
          &:hover,&_active {
            i {
              color: $color-main !important; }
            .sub-item_icon-link {
              border: 1px solid $color-main; } }
          i {
            font-size: 1.6rem;
            color: $color-grey-dark; } }
        &_icon-link {
          min-width: 3.3rem;
          min-height: 3.3rem;
          border: 1px solid $color-grey-dark;
          border-radius: 50%;
          display: flex;
          align-items: center;
          justify-content: center; } }
      .active {
        max-height: calc(4.5rem * #{$locations}); } }
    &-link {
      min-width: 4.5rem;
      min-height: 4.5rem;
      display: flex;
      align-items: center;
      text-decoration: none;
      transition: all .3s ease;
      &:hover {
        i {
          color: $color-main; }
        .icon-link {
          border: 1px solid $color-main;
          box-shadow: 0 0 14px $color-main; } }
      i {
        font-size: 2rem;
        color: $color-grey-dark; }
      .icon-link {
        min-width: 4.5rem;
        min-height: 4.5rem;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 1px solid $color-grey-dark;
        border-radius: 50%;
        &:hover {
          color: $color-main !important;
          border: 1px solid $color-main;
          box-shadow: 0 0 14px $color-main; } }
      &_active {
        .icon-link {
          border: 1px solid $color-main;
          box-shadow: 0 0 14px $color-main; }
        i {
          color: $color-main !important; } }
      .arrow {
        position: absolute;
        right: 1.5rem;
        transition: all .2s ease;
        &_active {
          transform: rotate(90deg); } } }
    &-active {
      width: 30rem;
      background-color: white;
      box-shadow: 0 0 20px 10px white;
      .desc {
        white-space: normal; } } }

  &__log-out {
    width: 4.5rem;
    height: 4.5rem;
    position: absolute;
    bottom: 0;
    margin: 3rem;
    transition: all .3s ease;
    &:hover {
      i {
        border: 1px solid #ff1b1b;
        box-shadow: 0 0 13px #ff1b1b;
        color: #ff1b1b; } }
    i {
      width: 4.5rem;
      height: 4.5rem;
      font-size: 2rem;
      color: $color-grey-dark;
      background-color: white;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 1px solid $color-grey-dark;
      border-radius: 50%;
      box-shadow: 0 0 13px white; } } }



.overlay {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(49, 49, 49, 0.44);
  visibility: hidden;
  opacity: 0;
  transition: all 0.6s ease-in-out;
  &-active {
    opacity: 1;
    visibility: visible; } }






